/*login register forms  */

form.login, form.register {
  width: 40%;
  margin: 2em auto 0;
  text-align: center;
}

.login fieldset, .register fieldset {
  border-radius: 0.3em;
  padding: 0.8em;
  background-color: #ffffff;
}

.login h2, .register h2 {
  padding-top: 0;
  border-bottom: none;
}

.login p, .register p {
  padding-bottom: 0;
  padding-top: 1.5rem;
}

.login input, .register input {
  flex: 0 1 100%;
  border: 1px solid;
  padding: 0.3em;
  border-left: 0.3em solid green;
  border-top-right-radius: 0.3em;
  border-bottom-right-radius: 0.3em;
}

.login select, .register select {
  border-color: black;
}

.login button, .register button {
  color: white;
  background-color: #bd9922;
  border: none;
  border-radius: 0.3em;
  padding: 0.6em 1.2em;
  margin-top: 1.5rem;
  width: 60%;
  outline: none;
}

.login i, .register i {
  border: 1px solid;
  border-right: none;
  padding: 0.55em;
  border-top-left-radius: 0.3em;
  border-bottom-left-radius: 0.3em;
  background-color: #e9ecef;
}

.login a, .register a {
  color: #007bff;
}

.login a:hover, .register a:hover {
  cursor: pointer;
}

.register .tel {
  padding-right: 0.8em;
}

.login .field, .register .field {
  display: flex;
  padding-bottom: 0;
}

/* errors  */

p.error {
  text-align: left;
  border-radius: 0.3em;
  color: red;
  padding-top: 0;
}

.input-error {
  border-left-color: red;
}